
import React, { useState, } from "react";
import axios from "axios";
import {
  CCard,
  CCardBody,
  CCol,
  CCardHeader,
  CButton,
  CFormInput,
  CFormLabel,
  CForm,
  CImage,
} from "@coreui/react";
import Swal from "sweetalert2";
import { CBreadcrumb, CBreadcrumbItem } from '@coreui/react'
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
// or

const AddPracticeArea = () => {
  const token = typeof window !== "undefined" ? localStorage.getItem("status") : null;
  const Toast = Swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", Swal.stopTimer);
      toast.addEventListener("mouseleave", Swal.resumeTimer);
    },
  });
  const navigate = useNavigate();
  const [practiceArea, setPracticeArea] = useState("");
  const [image, setImage] = useState("");

  const imageChange = (e) => {
    if (e.target.files && e.target.files.length > 0) {
      setImage(e.target.files[0]);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const helpdetailsobj = new FormData();
    helpdetailsobj.append("name", practiceArea);
    helpdetailsobj.append("image", image);

    await axios
      .post("addEditPractice", helpdetailsobj, {
        headers: {
          Accept: "application/json",
          'Authorization': 'Bearer '+ token
        },
      })
      .then((result) => {
        console.log(result);

        // result.data.status == 400
        //    ? Toast.fire({
        //        icon: "error",
        //        title: result.data.message,
        //      })
        //    : 
           Toast.fire({
               icon: "success",
               title: result.data.message,
             });
 
         result.data.status == 400 ? navigate("/add-practice-area") : navigate("/practice-area");
      })
      .catch((error) => {
        Toast.fire({
          icon: "error",
          title: error.response.data.message,
        });
      });
  };

  return (
    <>
      <div className="px-2">
      <CCol xs={12} >
          <CCard className="p-2  mb-3">
          <CBreadcrumb className="m-0 ms-2">
      <CBreadcrumbItem href="/dashboard" style={{textDecoration:"none !important"}}>Dashboard</CBreadcrumbItem>
     
          <CBreadcrumbItem  href="/practice-area">
           Practice Area
          </CBreadcrumbItem>
          <CBreadcrumbItem active>Add Practice Area</CBreadcrumbItem>
  
    </CBreadcrumb>
          </CCard>
        </CCol>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
              <strong>Add Practice Area</strong>
            </CCardHeader>
            <CCardBody>
              <CForm className="row g-3" onSubmit={handleSubmit}>
                <CCol xs={12}>
                  <CFormLabel htmlFor="inputAddress">Practice Area</CFormLabel>
                  <CFormInput
                    id="inputAddress"
                    name="name"
                    // value={city}
                    onChange={(e) => setPracticeArea(e.target.value)}
                    type="text"
                    required
                    placeholder="Practice Area name"
                  />
                </CCol>

{/* 
                <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Profile :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          {image == null ? (
                            <span style={{ color: "red" }}>
                              No Profile Image
                            </span>
                          ) : (
                            <Link to={image} target="_blank">
                              {" "}
                              <CImage
                                // align="center"
                                width={75}
                                rounded
                                thumbnail
                                orientation="top"
                                src={image}
                              />{" "}
                            </Link>
                          )}
                        </CCol>
                      </CCol> */}

                      <CCol md={6}>
                  <CFormLabel htmlFor="inputAddress">Company Logo</CFormLabel>
                  <CFormInput
                    type="file"
                    accept="image/*"
                    onChange={imageChange}
                    id="formFile"
                  />
                </CCol>

                <CCol md={3} className="pt-3 ">
                  {image && (
                    <CImage
                      align="center"
                      width={75}
                      rounded
                      thumbnail
                      orientation="top"
                      src={URL.createObjectURL(image)}
                    />
                  )}
                </CCol>

                <CCol xs={12} className="text-center">
                  <CButton type="submit" className="bg-black text-white border-b">
                 Submit
                  </CButton>
                </CCol>
              </CForm>

            </CCardBody>
          </CCard>
        </CCol>
      </div>
    </>
  );
};

export default AddPracticeArea;
